.appWrapper {
    margin: 10px auto;
    min-width: auto;
    max-width: 1200px;
    display: grid;
    grid-template-areas:
    "h h"
    "n c";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
}

.appWrapperContent {
    grid-area: c;
    background-color: green;
}

